

























import Vue from 'vue'
import Tab from './Tab.vue'

export default Vue.extend({
  components: {
    Tab
  },
  model: {
    prop: 'currentEntry',
    event: 'entrySelected'
  },
  props: {
    items: {
      type: Map,
      required: true
    },
    currentEntry: {
      type: String,
      required: true
    }
  },

  watch: {
  },

  methods: {
    onEntrySelected (val: string): void {
      this.$emit('entrySelected', val)
    }
  }
})
