






















































import Vue from 'vue'
export default Vue.extend({
  model: {
    prop: 'currentEntry',
    event: 'entrySelected'
  },
  props: {
    name: {
      type: String,
      required: true
    },
    set: {
      type: Set,
      required: true
    },
    currentEntry: {
      type: String,
      required: true
    }
  },

  computed: {
    entries () {
      const data = [...this.set.values()] as string[]
      return data
    }
  },

  mounted() {
  },

  methods: {
    entrySelected (key: string) {
      this.$emit('entrySelected', key)
    }
  }
})
